/*!media*/
.wrap {
  height: 100%;
}
.top .bg {
  height: 12rem;
  background: url('../assets/images/etanalu2.jpg') no-repeat;
  background-size: 100%;
}
.primary-label {
  text-align: center;
}
.primary-label span {
  color: #e74793;
  font-size: 2rem;
  line-height: 4rem;
}
.profile {
  background: #ffffff;
  position: relative;
  margin-top: -5rem;
  height: 10rem;
  -webkit-box-shadow: 0 1px 3px #cccccc;
          box-shadow: 0 1px 3px #cccccc;
}
.profile .row1,
.profile .row2 {
  height: 5rem;
}
.profile .profile-col {
  height: 5rem;
}
.profile .headPortrait {
  width: 6rem;
  height: 6rem;
  margin-top: -3rem;
}
.profile .nickName {
  left: 12rem;
  top: 1rem;
}
.profile .phone {
  left: 12rem;
  top: 3rem;
}
.cen .cen-col {
  height: 10rem;
}
.bom .cen-col .agent {
  width: 7rem;
  height: 7rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.bom .cen-col p {
  line-height: 1.4;
  margin: 0;
  padding: 0;
  display: block;
}
.list {
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}



